import { high } from 'nc-lightapp-front';

const { Refer } = high;
export default function (props = {}) {
var conf = {
refType: 'tree',
refName: '来源支付机构类别',/* 国际化处理： 收费项目*/
isCacheable: false,
rootNode: {
    refname: "来源支付机构类别",
    refpk: "root"
},
queryTreeUrl: "/nccloud/hpf/ref/ChargetypeTreeRef.do",
treeConfig: {
name: ["编码", "名称"],
code: ["refcode", "refname"]
},

};
return <Refer {...Object.assign(conf, props)} />
}
